<script>
    export default {
        name: "Info",
        data(){
            return {
                user:{},
                isShow1:false,
                isShow2:false,
            }
        },
        created(){
            this.user = JSON.parse(localStorage.getItem('profile'));
            // console.log(this.user)
            // this.$router.push("/setusername");
            // localStorage.setItem("setusername",JSON.stringify(this.user));
        },
        methods:{
            //退出登录
            signout(){
                this.axios.get("/v2/signout").then(res=>{
                    // console.log(res);
                    this.$router.push("/profile");
                    localStorage.setItem("message",JSON.stringify(res.message));
                    // localStorage.removeItem(username)
                }).catch(err=>{});
            }
        }
    }
</script>

